.Statistic {
    &__label {
    	text-transform: uppercase;
        font-size: 13px;
        font-weight: 300;
        margin-bottom: 5px;
    }
    &_2col {
		display: flex;
		align-items: center;
		justify-content: space-between;

		&:not(:last-child) {
			margin-bottom: 30px;
		}
    }
    &__balance {
		font-size: 37px;
    }
}  